'use client';

import { useState } from 'react';
import StarRating from './StarRating';

interface WriteReviewFormProps {
  productId: string;
  productName: string;
  onSubmit: (review: {
    rating: number;
    title: string;
    content: string;
    images: File[];
  }) => void;
  onCancel: () => void;
}

const WriteReviewForm = ({ productId, productName, onSubmit, onCancel }: WriteReviewFormProps) => {
  const [rating, setRating] = useState(0);
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [images, setImages] = useState<File[]>([]);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(e.target.files || []);
    if (images.length + files.length <= 5) {
      setImages(prev => [...prev, ...files]);
    } else {
      alert('You can upload maximum 5 images');
    }
  };

  const removeImage = (index: number) => {
    setImages(prev => prev.filter((_, i) => i !== index));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (rating === 0) {
      alert('Please select a rating');
      return;
    }
    
    if (title.trim().length < 5) {
      alert('Title must be at least 5 characters long');
      return;
    }
    
    if (content.trim().length < 20) {
      alert('Review must be at least 20 characters long');
      return;
    }

    setIsSubmitting(true);
    
    try {
      await onSubmit({
        rating,
        title: title.trim(),
        content: content.trim(),
        images
      });
      
      // Reset form
      setRating(0);
      setTitle('');
      setContent('');
      setImages([]);
    } catch (error) {
      console.error('Error submitting review:', error);
      alert('Failed to submit review. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  const getRatingText = (rating: number) => {
    switch (rating) {
      case 1: return 'Poor';
      case 2: return 'Fair';
      case 3: return 'Good';
      case 4: return 'Very Good';
      case 5: return 'Excellent';
      default: return '';
    }
  };

  return (
    <div className="max-w-4xl mx-auto bg-white rounded-lg shadow-lg p-6">
      <div className="border-b pb-4 mb-6">
        <h2 className="text-2xl font-bold text-gray-900">Write a customer review</h2>
        <p className="text-gray-600 mt-1">Share your thoughts about {productName}</p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Rating Section */}
        <div>
          <label className="block text-lg font-medium text-gray-900 mb-3">
            Overall rating
          </label>
          <div className="flex items-center gap-4">
            <StarRating
              rating={rating}
              size="lg"
              interactive
              onRatingChange={setRating}
            />
            {rating > 0 && (
              <span className="text-lg font-medium text-gray-700">
                {getRatingText(rating)}
              </span>
            )}
          </div>
          <p className="text-sm text-gray-500 mt-2">
            Click on the stars to select your rating
          </p>
        </div>

        {/* Review Title */}
        <div>
          <label htmlFor="title" className="block text-lg font-medium text-gray-900 mb-2">
            Add a headline
          </label>
          <input
            type="text"
            id="title"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder="What's most important to know?"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
            maxLength={100}
          />
          <div className="flex justify-between text-sm text-gray-500 mt-1">
            <span>Help others learn what makes this product special</span>
            <span>{title.length}/100</span>
          </div>
        </div>

        {/* Review Content */}
        <div>
          <label htmlFor="content" className="block text-lg font-medium text-gray-900 mb-2">
            Add a written review
          </label>
          <textarea
            id="content"
            value={content}
            onChange={(e) => setContent(e.target.value)}
            placeholder="What did you like or dislike? What did you use this product for?"
            rows={6}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent resize-vertical"
            maxLength={5000}
          />
          <div className="flex justify-between text-sm text-gray-500 mt-1">
            <span>Share your experience with other customers</span>
            <span>{content.length}/5000</span>
          </div>
        </div>

        {/* Image Upload */}
        <div>
          <label className="block text-lg font-medium text-gray-900 mb-2">
            Add photos (optional)
          </label>
          <div className="border-2 border-dashed border-gray-300 rounded-md p-6 text-center hover:border-orange-500 transition-colors">
            <input
              type="file"
              accept="image/*"
              multiple
              onChange={handleImageUpload}
              className="hidden"
              id="image-upload"
            />
            <label htmlFor="image-upload" className="cursor-pointer">
              <svg className="w-12 h-12 text-gray-400 mx-auto mb-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
              </svg>
              <p className="text-gray-600">Click to upload images</p>
              <p className="text-sm text-gray-500 mt-1">PNG, JPG up to 10MB each (max 5 images)</p>
            </label>
          </div>

          {/* Image Previews */}
          {images.length > 0 && (
            <div className="mt-4 grid grid-cols-2 md:grid-cols-5 gap-4">
              {images.map((image, index) => (
                <div key={index} className="relative">
                  <img
                    src={URL.createObjectURL(image)}
                    alt={`Preview ${index + 1}`}
                    className="w-full h-24 object-cover rounded-md border"
                  />
                  <button
                    type="button"
                    onClick={() => removeImage(index)}
                    className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full w-6 h-6 flex items-center justify-center text-sm hover:bg-red-600 transition-colors"
                  >
                    ×
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Review Guidelines */}
        <div className="bg-yellow-50 border border-yellow-200 rounded-md p-4">
          <h4 className="font-medium text-gray-900 mb-2">Review guidelines</h4>
          <ul className="text-sm text-gray-700 space-y-1">
            <li>• Focus on the product and your experience using it</li>
            <li>• Be honest and fair in your assessment</li>
            <li>• Don't include personal information or spam</li>
            <li>• Keep language family-friendly</li>
          </ul>
        </div>

        {/* Submit Buttons */}
        <div className="flex gap-4 pt-4">
          <button
            type="submit"
            disabled={isSubmitting || rating === 0}
            className="px-6 py-3 bg-orange-600 text-white rounded-md hover:bg-orange-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors font-medium"
          >
            {isSubmitting ? 'Submitting...' : 'Submit Review'}
          </button>
          <button
            type="button"
            onClick={onCancel}
            className="px-6 py-3 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors font-medium"
          >
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
};

export default WriteReviewForm;
