'use client';

import { useState, useMemo } from 'react';
import { Review, getReviewsForProduct, getReviewSummary, sortReviews, filterReviewsByRating } from '@/data/reviews';
import StarRating, { RatingBreakdown } from './StarRating';
import WriteReviewForm from './WriteReviewForm';

interface ReviewsProps {
  productId: string;
  productName: string;
}

const Reviews = ({ productId, productName }: ReviewsProps) => {
  const [showWriteReview, setShowWriteReview] = useState(false);
  const [sortBy, setSortBy] = useState<'newest' | 'oldest' | 'highest' | 'lowest' | 'helpful'>('newest');
  const [filterRating, setFilterRating] = useState<number | null>(null);
  const [showAllReviews, setShowAllReviews] = useState(false);
  const [helpfulVotes, setHelpfulVotes] = useState<{ [key: string]: { helpful: boolean; notHelpful: boolean } }>({});

  const allReviews = getReviewsForProduct(productId);
  const reviewSummary = getReviewSummary(productId);

  const filteredAndSortedReviews = useMemo(() => {
    let filtered = filterReviewsByRating(allReviews, filterRating);
    return sortReviews(filtered, sortBy);
  }, [allReviews, filterRating, sortBy]);

  const displayedReviews = showAllReviews ? filteredAndSortedReviews : filteredAndSortedReviews.slice(0, 5);

  const handleWriteReview = async (reviewData: {
    rating: number;
    title: string;
    content: string;
    images: File[];
  }) => {
    // In a real app, this would submit to an API
    console.log('Submitting review:', reviewData);
    setShowWriteReview(false);
    // You could update local state or refresh reviews here
  };

  const handleHelpfulVote = (reviewId: string, isHelpful: boolean) => {
    setHelpfulVotes(prev => ({
      ...prev,
      [reviewId]: {
        helpful: isHelpful && !prev[reviewId]?.helpful,
        notHelpful: !isHelpful && !prev[reviewId]?.notHelpful
      }
    }));
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  if (showWriteReview) {
    return (
      <WriteReviewForm
        productId={productId}
        productName={productName}
        onSubmit={handleWriteReview}
        onCancel={() => setShowWriteReview(false)}
      />
    );
  }

  return (
    <div className="bg-white">
      {/* Reviews Summary */}
      <div className="border-b pb-6 mb-6">
        <h2 className="text-2xl font-bold text-gray-900 mb-4">Customer reviews</h2>
        
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Overall Rating */}
          <div>
            <div className="flex items-center gap-4 mb-4">
              <StarRating 
                rating={reviewSummary.averageRating} 
                size="lg" 
                showCount 
                reviewCount={reviewSummary.totalReviews}
              />
            </div>
            <p className="text-lg font-medium text-gray-900">
              {reviewSummary.averageRating.toFixed(1)} out of 5 stars
            </p>
            <p className="text-gray-600">{reviewSummary.totalReviews} global ratings</p>
          </div>
          
          {/* Rating Breakdown */}
          <div>
            <RatingBreakdown ratings={reviewSummary.ratingBreakdown} />
          </div>
        </div>

        {/* Write Review Button */}
        <button
          onClick={() => setShowWriteReview(true)}
          className="mt-6 px-6 py-3 bg-orange-600 text-white rounded-md hover:bg-orange-700 transition-colors font-medium"
        >
          Write a customer review
        </button>
      </div>

      {/* Filters and Sorting */}
      <div className="flex flex-col sm:flex-row gap-4 mb-6">
        <div className="flex items-center gap-2">
          <label className="text-sm font-medium text-gray-700">Sort by:</label>
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value as any)}
            className="border border-gray-300 rounded-md px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-orange-500"
          >
            <option value="newest">Most recent</option>
            <option value="oldest">Oldest</option>
            <option value="highest">Highest rated</option>
            <option value="lowest">Lowest rated</option>
            <option value="helpful">Most helpful</option>
          </select>
        </div>

        <div className="flex items-center gap-2">
          <label className="text-sm font-medium text-gray-700">Filter by:</label>
          <select
            value={filterRating || ''}
            onChange={(e) => setFilterRating(e.target.value ? Number(e.target.value) : null)}
            className="border border-gray-300 rounded-md px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-orange-500"
          >
            <option value="">All ratings</option>
            <option value="5">5 stars</option>
            <option value="4">4 stars</option>
            <option value="3">3 stars</option>
            <option value="2">2 stars</option>
            <option value="1">1 star</option>
          </select>
        </div>
      </div>

      {/* Reviews List */}
      <div className="space-y-6">
        {displayedReviews.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-gray-500 text-lg">No reviews found for the selected filters.</p>
            <button
              onClick={() => setFilterRating(null)}
              className="mt-2 text-orange-600 hover:text-orange-700"
            >
              Clear filters
            </button>
          </div>
        ) : (
          displayedReviews.map((review) => (
            <ReviewItem
              key={review.id}
              review={review}
              onHelpfulVote={handleHelpfulVote}
              userVote={helpfulVotes[review.id]}
            />
          ))
        )}
      </div>

      {/* Show More Button */}
      {filteredAndSortedReviews.length > 5 && !showAllReviews && (
        <div className="text-center mt-8">
          <button
            onClick={() => setShowAllReviews(true)}
            className="px-6 py-3 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors font-medium"
          >
            See all {filteredAndSortedReviews.length} reviews
          </button>
        </div>
      )}
    </div>
  );
};

// Individual Review Item Component
const ReviewItem = ({ 
  review, 
  onHelpfulVote, 
  userVote 
}: { 
  review: Review; 
  onHelpfulVote: (reviewId: string, isHelpful: boolean) => void;
  userVote?: { helpful: boolean; notHelpful: boolean };
}) => {
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  return (
    <div className="border-b pb-6 last:border-b-0">
      <div className="flex items-start gap-4">
        {/* User Avatar */}
        <div className="flex-shrink-0">
          <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center">
            {review.userAvatar ? (
              <img 
                src={review.userAvatar} 
                alt={review.userName}
                className="w-10 h-10 rounded-full object-cover"
              />
            ) : (
              <span className="text-gray-600 font-medium">
                {review.userName.charAt(0).toUpperCase()}
              </span>
            )}
          </div>
        </div>

        {/* Review Content */}
        <div className="flex-1">
          <div className="flex items-center gap-2 mb-2">
            <span className="font-medium text-gray-900">{review.userName}</span>
            {review.verifiedPurchase && (
              <span className="text-xs bg-orange-100 text-orange-800 px-2 py-1 rounded">
                Verified Purchase
              </span>
            )}
          </div>

          <div className="flex items-center gap-2 mb-2">
            <StarRating rating={review.rating} size="sm" />
            <span className="text-sm text-gray-600">{formatDate(review.createdAt)}</span>
          </div>

          <h4 className="font-semibold text-gray-900 mb-2">{review.title}</h4>
          <p className="text-gray-700 mb-4 leading-relaxed">{review.content}</p>

          {/* Review Images */}
          {review.images && review.images.length > 0 && (
            <div className="flex gap-2 mb-4">
              {review.images.map((image, index) => (
                <img
                  key={index}
                  src={image}
                  alt={`Review image ${index + 1}`}
                  className="w-20 h-20 object-cover rounded border cursor-pointer hover:opacity-80 transition-opacity"
                  onClick={() => {/* Open image modal */}}
                />
              ))}
            </div>
          )}

          {/* Helpful Buttons */}
          <div className="flex items-center gap-4 text-sm">
            <span className="text-gray-600">Helpful?</span>
            <button
              onClick={() => onHelpfulVote(review.id, true)}
              className={`flex items-center gap-1 px-3 py-1 rounded border transition-colors ${
                userVote?.helpful 
                  ? 'bg-orange-100 border-orange-300 text-orange-700' 
                  : 'border-gray-300 text-gray-600 hover:bg-gray-50'
              }`}
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14 10h4.764a2 2 0 011.789 2.894l-3.5 7A2 2 0 0115.263 21h-4.017c-.163 0-.326-.02-.485-.06L7 20m7-10V8a2 2 0 00-2-2h-.095c-.5 0-.905.405-.905.905 0 .714-.211 1.412-.608 2.006L7 11v9m7-10h-2M7 20H5a2 2 0 01-2-2v-6a2 2 0 012-2h2.5" />
              </svg>
              Yes ({review.helpfulVotes + (userVote?.helpful ? 1 : 0)})
            </button>
            <button
              onClick={() => onHelpfulVote(review.id, false)}
              className={`flex items-center gap-1 px-3 py-1 rounded border transition-colors ${
                userVote?.notHelpful 
                  ? 'bg-gray-200 border-gray-400 text-gray-700' 
                  : 'border-gray-300 text-gray-600 hover:bg-gray-50'
              }`}
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 14H5.236a2 2 0 01-1.789-2.894l3.5-7A2 2 0 018.736 3h4.018c.163 0 .326.02.485.06L17 4m-7 10v2a2 2 0 002 2h.095c.5 0 .905-.405.905-.905 0-.714.211-1.412.608-2.006L17 13V4m-7 10h2M17 4h2a2 2 0 012 2v6a2 2 0 01-2 2h-2.5" />
              </svg>
              No ({review.totalVotes - review.helpfulVotes + (userVote?.notHelpful ? 1 : 0)})
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Reviews;
