'use client';

import { useState } from 'react';

interface StarRatingProps {
  rating: number;
  totalStars?: number;
  size?: 'sm' | 'md' | 'lg';
  interactive?: boolean;
  onRatingChange?: (rating: number) => void;
  showCount?: boolean;
  reviewCount?: number;
}

const StarRating = ({ 
  rating, 
  totalStars = 5, 
  size = 'md', 
  interactive = false,
  onRatingChange,
  showCount = false,
  reviewCount = 0
}: StarRatingProps) => {
  const [hoveredRating, setHoveredRating] = useState(0);
  const [selectedRating, setSelectedRating] = useState(rating);

  const sizes = {
    sm: 'w-4 h-4',
    md: 'w-5 h-5',
    lg: 'w-6 h-6'
  };

  const textSizes = {
    sm: 'text-sm',
    md: 'text-base',
    lg: 'text-lg'
  };

  const handleStarClick = (starRating: number) => {
    if (interactive) {
      setSelectedRating(starRating);
      onRatingChange?.(starRating);
    }
  };

  const handleStarHover = (starRating: number) => {
    if (interactive) {
      setHoveredRating(starRating);
    }
  };

  const handleMouseLeave = () => {
    if (interactive) {
      setHoveredRating(0);
    }
  };

  const displayRating = interactive ? (hoveredRating || selectedRating) : rating;

  return (
    <div className="flex items-center gap-2">
      <div 
        className="flex items-center gap-1"
        onMouseLeave={handleMouseLeave}
      >
        {Array.from({ length: totalStars }, (_, index) => {
          const starValue = index + 1;
          const isFilled = starValue <= displayRating;
          const isPartial = !isFilled && starValue - 0.5 <= displayRating;

          return (
            <button
              key={index}
              type="button"
              className={`${sizes[size]} ${interactive ? 'cursor-pointer hover:scale-110 transition-transform' : 'cursor-default'} focus:outline-none`}
              onClick={() => handleStarClick(starValue)}
              onMouseEnter={() => handleStarHover(starValue)}
              disabled={!interactive}
            >
              {isFilled ? (
                <svg className="w-full h-full text-yellow-400 fill-current" viewBox="0 0 24 24">
                  <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                </svg>
              ) : isPartial ? (
                <svg className={`w-full h-full ${sizes[size]}`} viewBox="0 0 24 24">
                  <defs>
                    <linearGradient id={`half-${index}`}>
                      <stop offset="50%" stopColor="#facc15" />
                      <stop offset="50%" stopColor="#e5e7eb" />
                    </linearGradient>
                  </defs>
                  <path 
                    fill={`url(#half-${index})`}
                    d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" 
                  />
                </svg>
              ) : (
                <svg className="w-full h-full text-gray-300 fill-current" viewBox="0 0 24 24">
                  <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                </svg>
              )}
            </button>
          );
        })}
      </div>
      
      {!interactive && (
        <div className="flex items-center gap-2">
          <span className={`${textSizes[size]} font-medium text-gray-700`}>
            {rating.toFixed(1)}
          </span>
          {showCount && reviewCount > 0 && (
            <span className={`${textSizes[size]} text-blue-600 hover:text-blue-800 cursor-pointer`}>
              ({reviewCount.toLocaleString()} {reviewCount === 1 ? 'review' : 'reviews'})
            </span>
          )}
        </div>
      )}
      
      {interactive && hoveredRating > 0 && (
        <span className={`${textSizes[size]} text-gray-600 ml-2`}>
          {hoveredRating} star{hoveredRating !== 1 ? 's' : ''}
        </span>
      )}
    </div>
  );
};

// Compact rating component for product cards
export const CompactRating = ({ rating, reviewCount }: { rating: number; reviewCount: number }) => {
  return (
    <div className="flex items-center gap-1">
      <div className="flex">
        {Array.from({ length: 5 }, (_, index) => {
          const isFilled = index < Math.floor(rating);
          const isPartial = index === Math.floor(rating) && rating % 1 >= 0.5;
          
          return (
            <svg 
              key={index} 
              className="w-3 h-3 text-yellow-400 fill-current" 
              viewBox="0 0 24 24"
            >
              {isFilled ? (
                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
              ) : isPartial ? (
                <defs>
                  <linearGradient id={`compact-half-${index}`}>
                    <stop offset="50%" stopColor="#facc15" />
                    <stop offset="50%" stopColor="#e5e7eb" />
                  </linearGradient>
                </defs>
              ) : (
                <path className="text-gray-300" d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
              )}
            </svg>
          );
        })}
      </div>
      <span className="text-xs text-gray-600">({reviewCount})</span>
    </div>
  );
};

// Amazon-style rating breakdown
export const RatingBreakdown = ({ ratings }: { ratings: { [key: number]: number } }) => {
  const totalReviews = Object.values(ratings).reduce((sum, count) => sum + count, 0);
  
  return (
    <div className="space-y-2">
      {[5, 4, 3, 2, 1].map((stars) => {
        const count = ratings[stars] || 0;
        const percentage = totalReviews > 0 ? (count / totalReviews) * 100 : 0;
        
        return (
          <div key={stars} className="flex items-center gap-2 text-sm">
            <span className="text-blue-600 hover:text-blue-800 cursor-pointer">
              {stars} star
            </span>
            <div className="flex-1 bg-gray-200 rounded-full h-2">
              <div 
                className="bg-yellow-400 h-2 rounded-full transition-all duration-300"
                style={{ width: `${percentage}%` }}
              />
            </div>
            <span className="text-gray-600 w-8 text-right">
              {Math.round(percentage)}%
            </span>
          </div>
        );
      })}
    </div>
  );
};

export default StarRating;
